.footer {
    height: 150px;
    background-color: darkslategrey;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

}
.footerContainer {
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.title {
    background-color: lemonchiffon;
    margin-top: 10px;
    padding: 5px;
}
.container {
    display: flex;
    justify-content: space-between;
    border: 2px solid;
    width: 250px;

}
.box {
    height: 50px;
    width: 50px;
    background-color: lemonchiffon;
}
.text {
    background-color: lemonchiffon;
}