.projectsBlock {
    min-height: 100vh;
    background-color: darkslategrey;
}
.projectsContainer {
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;

}
.title {
    background-color: lightgrey;
}
.projects {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;

}