.project {
    display: flex;
    flex-direction: column;
    text-align: left;
    background-color: lightgrey;
    max-width: 300px;
    box-sizing: border-box;

}
.img {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    height: 300px;
    background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRLOMS064En_yXK19hWNTohPImHc-X-kePDZq-slG7aIXrCQf7talAJ-Euk75ZPRvWqQr0&usqp=CAU");
    background-size: cover;
    background-position: center;
}
.button {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color:  lightgrey;
    font-weight: bold;
    border-radius: 10px;
    width: 150px;
    height: 50px;
}
.title {
    margin: 10px;
    background-color: darkgrey;
}
.description {
    margin: 10px;
    background-color: darkgrey;

}