.contactsBlock {
    height: 100vh;
    background-color: whitesmoke;
}
.contactsContainer {
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    border: 2px solid;
}
.formContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 80%;
    height: 80%;
    background-color: lightgrey;
}
.title {
    background-color: lightgrey;
}
.form {
    height: 60%;
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: space-between;
    background-color: lightgrey;
    border: 2px solid;
}
.input {
    height: 40px;
}
.textarea {
    height: 100px;
}
.button {
    background-color: grey;
    font-weight: bold;
    height: 50px;
    width: 150px;
}